import axiosRequest from '@/utils/AxiosRequest';

let access_token = require('@/utils/Constant').default.requestToken

export function getWorkFlowList(page,size,keyword) {
  return axiosRequest({url: '/template/list', method: 'GET' ,params:{access_token,page,size,keyword}});
}

export function WorkFlowListadd(params) {
  return axiosRequest({url: `/template/add?access_token=${access_token}`, method: 'POST', data: params});
}


export function WorkFlowListAddOrRevise(params) {
  return axiosRequest({url: `/template/view-node/design?access_token=${access_token}`, method: 'PATCH', data: params});
}

export function getWorkFlowListDetail(template_id) {
  return axiosRequest({url: `/template/${template_id}`, method: 'GET' ,params:{access_token}});
}

export function WorkFlowListupdate(params) {
  return axiosRequest({url: `/template/update?access_token=${access_token}`, method: 'PUT', data: params});
}

export function WorkFlowListDelete(params) {
  return axiosRequest({url: `/template/delete?access_token=${access_token}`, method: 'DELETE', data: params});
}

export function getRoleList() {
  return axiosRequest({url: '/role/list', method: 'GET' ,params:{access_token}});
}

export function RoleaAdd(params) {
  return axiosRequest({url: `/role/add?access_token=${access_token}`, method: 'POST', data: params});
}

export function RoleListupdate(params) {
  return axiosRequest({url: `/role/update?access_token=${access_token}`, method: 'PUT', data: params});
}

export function RoleDelete(params) {
  return axiosRequest({url: `/role/delete?access_token=${access_token}`, method: 'DELETE', data: params});
}

export function getWorkdynamic(wfe_id) {
  return axiosRequest({url: `/process-instance/dynamic/${wfe_id}`, method: 'GET' ,params:{ access_token}});
}

export function getWorkTobeDealt(open_id,type,page,size) {
  return axiosRequest({url: `/process-instance/to-be-dealt`, method: 'GET' ,params:{access_token,open_id,type,page,size}});
}

export function userList(keyword) {
  return axiosRequest({url: `/user/list?keyword=${keyword}`, method: 'GET' ,params:{access_token}});
}
export function userListtemp() {
  return axiosRequest({url: `/user/list?keyword=`, method: 'GET' ,params:{access_token}});
}


export function userPostList() {
  return axiosRequest({url: `/post/list`, method: 'GET' ,params:{access_token}});
}
