<template>
  <div class="process-box">
    <a-tabs @change="callback" type="card">
      <a-tab-pane tab="待处理" key="3" class="font-color">
        <a-table
          rowKey="wfeId"
          :bordered="showBorder"
          :dataSource="TobeProcessed"
          :columns="columns"
          :scroll="{ y: 330}"
          :pagination="showPagination"
          class="no-scroll"
          style="height:calc(100vh - 220px);"
        >
          <template
            slot="title"
            slot-scope="text"
            style="overflow: hidden;text-overflow:ellipsis;width:150px;"
          >
            <a-tooltip placement="topLeft" :title="text">
              <span class="process-title">{{text}}</span>
            </a-tooltip>
          </template>
          <template slot="status" slot-scope="text,record">
            <span v-if="text=='N'">审核中</span>
            <span v-else-if="text=='Y'">已批准</span>
            <span v-if="text =='R'">驳回</span>
          </template>

          <template slot="priority" slot-scope="text,record">
            <img class="fire" v-if="text>1" src="../../assets/images/fire.png" alt="火苗" />
            <img class="fire" v-if="text>2" src="../../assets/images/fire.png" alt="火苗" />
          </template>

          <template slot="operation" slot-scope="text,record">
            <a href="javascript:;" @click="processbtn(record.wfeId)" style="color:#00aaee;">审批处理</a>
          </template>
        </a-table>
      </a-tab-pane>
      <a-tab-pane tab="我启动" key="1" class="font-color">
        <a-table
          rowKey="wfeId"
          :bordered="showBorder"
          :dataSource="startedDataSource"
          :columns="columns"
          :scroll="{ y: 330}"
          :pagination="showPagination"
          class="no-scroll"
          style="height:calc(100vh - 220px);"
        >
          <template
            slot="title"
            slot-scope="text"
            style="overflow: hidden;text-overflow:ellipsis;width:150px;"
          >
            <a-tooltip placement="topLeft" :title="text">
              <span class="process-title">{{text}}</span>
            </a-tooltip>
          </template>

          <template slot="status" slot-scope="text,record">
            <span v-if="text=='N'">审核中</span>
            <span v-else-if="text=='Y'">已批准</span>
            <span v-if="text =='R'">驳回</span>
          </template>

          <template slot="priority" slot-scope="text,record">
            <img class="fire" v-if="text>1" src="../../assets/images/fire.png" alt="火苗" />
            <img class="fire" v-if="text>2" src="../../assets/images/fire.png" alt="火苗" />
          </template>

          <template slot="operation" slot-scope="text,record">
            <a href="javascript:;" @click="processbtn(record.wfeId)" style="color:#00aaee;">审批处理</a>
          </template>
        </a-table>
      </a-tab-pane>

      <a-tab-pane tab="我审批" key="2" class="font-color">
        <a-table
          rowKey="wfeId"
          :bordered="showBorder"
          :dataSource="approveDataSource"
          :columns="columns"
          :scroll="{ y: 330}"
          :pagination="showPagination"
          class="no-scroll"
          style="height:calc(100vh - 220px);"
        >
          <template
            slot="title"
            slot-scope="text"
            style="overflow: hidden;text-overflow:ellipsis;width:150px;"
          >
            <a-tooltip placement="topLeft" :title="text">
              <span class="process-title">{{text}}</span>
            </a-tooltip>
          </template>
          <template slot="status" slot-scope="text,record">
            <span v-if="text=='N'">审核中</span>
            <span v-else-if="text=='Y'">已批准</span>
            <span v-if="text =='R'">驳回</span>
          </template>

          <template slot="priority" slot-scope="text,record">
            <img class="fire" v-if="text>1" src="../../assets/images/fire.png" alt="火苗" />
            <img class="fire" v-if="text>2" src="../../assets/images/fire.png" alt="火苗" />
          </template>
          <template slot="operation" slot-scope="text,record">
            <a href="javascript:;" @click="processbtn(record.wfeId)" style="color:#00aaee;">审批处理</a>
          </template>
        </a-table>
      </a-tab-pane>

      <a-tab-pane tab="已结束" key="4" class="font-color">
        <a-table
          rowKey="wfeId"
          :bordered="showBorder"
          :dataSource="endedDataSource"
          :columns="columns"
          :scroll="{ y: 330}"
          :pagination="showPagination"
          class="no-scroll"
          style="height:calc(100vh - 220px);"
        >
          <template
            slot="title"
            slot-scope="text"
            style="overflow: hidden;text-overflow:ellipsis;width:150px;"
          >
            <a-tooltip placement="topLeft" :title="text">
              <span class="process-title">{{text}}</span>
            </a-tooltip>
          </template>
          <template slot="status" slot-scope="text,record">
            <span v-if="text=='N'">审核中</span>
            <span v-else-if="text=='Y'">已批准</span>
            <span v-if="text =='R'">驳回</span>
          </template>

          <template slot="priority" slot-scope="text,record">
            <img class="fire" v-if="text>1" src="../../assets/images/fire.png" alt="火苗" />
            <img class="fire" v-if="text>2" src="../../assets/images/fire.png" alt="火苗" />
          </template>
          <template slot="operation" slot-scope="text,record">
            <a href="javascript:;" @click="processbtn(record.wfeId)" style="color:#00aaee;">审批处理</a>
          </template>
        </a-table>
      </a-tab-pane>
    </a-tabs>

    <div class="pagination-box">
      <a-pagination
        @change="onChange"
        :pageSizeOptions="pageSizeOptions"
        :total="total"
        :showTotal="total => `共 ${total} 条`"
        showSizeChanger
        showQuickJumper
        :pageSize="pageSize"
        v-model="current"
        @showSizeChange="onShowSizeChange"
      >
        <template slot="buildOptionText" slot-scope="props">
          <span v-if="props.value!=='50'">{{props.value}}条/页</span>
          <span v-if="props.value==='50'">全部</span>
        </template>
      </a-pagination>
    </div>
  </div>
</template>


<script>
import { getWorkTobeDealt } from "../../api/UserApi";
import moment from "moment";
export default {
  data() {
    return {
      pageSizeOptions: ["10", "20", "30", "40", "50"],
      current: 1,
      pageSize: 10,
      pageNumber: 1,
      total: 0,
      keyword: "",
      showBorder: false,
      showPagination: false,
      dataSource: [],
      columns: [
        {
          //表格头部样式的设置
          title: "单据名",
          dataIndex: "title",
          width: "200px",
          align: "center",
          scopedSlots: { customRender: "title" }
        },
        {
          title: "单据编号",
          dataIndex: "shtId",
          scopedSlots: { customRender: "shtId" },
          width: "200px",
          align: "center"
        },
        {
          title: "菜单编号",
          dataIndex: "menuId",
          width: "200px",
          align: "center"
        },
        {
          title: "启动用户",
          dataIndex: "userName",
          width: "200px",
          align: "center"
        },
        {
          title: "流程状态",
          dataIndex: "status",
          scopedSlots: { customRender: "status" },
          width: "100px",
          align: "center"
        },
        {
          title: "紧急程度",
          dataIndex: "priority",
          width: "200px",
          align: "center",
          scopedSlots: { customRender: "priority" }
        },
        // {
        //   title: "上一步状态",
        //   dataIndex: "remark",
        //   width: "200px",
        //   align: "center"
        // },
        {
          title: "启动时间",
          dataIndex: "createTime",
          width: "200px",
          align: "center"
        },
        // {
        //   title: "到达时间",
        //   dataIndex: "",
        //   width: "200px",
        //   align: "center"
        // },

        {
          title: "实例号",
          dataIndex: "wfeId",
          width: "200px",
          align: "center"
        },
        {
          title: "操作",
          dataIndex: "operation",
          scopedSlots: { customRender: "operation" },
          align: "center",
          width: "300px"
        }
      ],
      TobeProcessed: [
        //   {
        //    title:'awdsasd',
        //    shtId:'1',
        //    menuId:'1',
        //    userName:'asdad',
        //    status:false,
        //    priority:'紧急',
        //    createTime:'2019-10-04',
        //    wfeId:'1'
        // },
        // {
        //    title:'awdsasd',
        //    shtId:'2',
        //    menuId:"2",
        //    userName:'asdad',
        //    status:false,
        //    priority:'紧急',
        //    createTime:'2019-10-04',
        //    wfeId:'2'
        // }
      ],
      startedDataSource: [],
      approveDataSource: [],
      endedDataSource: [],
      type: 3
    };
  },
  mounted() {
    this.getWorkTobeDealtList();
  },
  methods: {
    callback(key) {
      console.log(key);
      this.type = key;
      this.getWorkTobeDealtList();
    },
    getWorkTobeDealtList() {
      console.log(this.$route.query.open_id);
      let type = this.type,
        open_id = this.$route.query.open_id,
        page = this.pageNumber,
        size = this.pageSize;
      // console.log(this.$route.query.user_id);

      getWorkTobeDealt(open_id, type, page, size).then(res => {
        //  console.log(res.data.records);
        this.total = res.data.total;
        if (type == 1) {
          this.startedDataSource = res.data.records; //我启动
          console.log(this.startedDataSource, "我启动");
          this.startedDataSource.forEach((item, i) => {
            if (this.startedDataSource.length)
              this.startedDataSource[i].createTime = moment(item.createTime).format("YYYY-MM-DD HH:mm:ss");
          });
        }
        if (type == 2) {
          this.approveDataSource = res.data.records; //2：我审批
          this.approveDataSource.forEach((item, i) => {
            if (this.approveDataSource.length)
              this.approveDataSource[i].createTime = moment(
                item.createTime
              ).format("YYYY-MM-DD HH:mm:ss");
          });
        }
        if (type == 3) {
          this.TobeProcessed = res.data.records; //3：待处理
          //  this.TobeProcessed = this.TobeProcessed
          this.TobeProcessed.forEach((item, i) => {
            if (this.TobeProcessed.length)
              this.TobeProcessed[i].createTime = moment(item.createTime).format(
                "YYYY-MM-DD HH:mm:ss"
              );
          });
        }
        if (type == 4) {
          this.endedDataSource = res.data.records; //
          this.endedDataSource.forEach((item, i) => {
            if (this.endedDataSource.length)
              this.endedDataSource[i].createTime = moment(
                item.createTime
              ).format("YYYY-MM-DD HH:mm:ss");
          });
        }

        console.log(this.startedDataSource);
      });
      console.log(this.TobeProcessed, "=======待处理的列表");
    },
    processbtn(wfe_id) {
      console.log(wfe_id);
      if (this.$route.name == "table")
        return this.$store.dispatch("processaddTab", wfe_id);
      let routeUrl = this.$router.resolve({
        name: "dynamicpage",
        query: {
          wfe_id,
          access_token: this.$global.requestToken
        }
      });
      window.open(routeUrl.href, "_blank");
    },
    onShowSizeChange(current, pageSize) {
      this.pageSize = pageSize;
      // console.log(this.pageSize);
      // console.log(current);
      this.getListdata();
    },
    // 右边的页码
    onChange(pageNumber) {
      this.pageNumber = pageNumber;
      // console.log("Page: ", pageNumber);
      this.getListdata();
    }
  }
};
</script>

<style lang="less" scoped>
.fire {
  width: 25px;
}
</style>
